        '<td>' + (resultAjax.expert_academic.amount2 || '') + '</td>' +
        '<td>' + (resultAjax.expert_academic.h_index || '') + '</td>' +
        '<td>' + (resultAjax.expert_academic.core || '') + '</td>' +
        '<td>' + (resultAjax.expert_academic.cssci || '') + '</td>' +
        '<td>' + (resultAjax.expert_academic.rdfybkzl || '') + '</td>'
      jqResearcherAcademicContent.empty()
        .append(htmlTemplate)

      jqThisResearcherName.empty()
        .append(resultAjax.expert_academic.name || '')

      jqThisResearcherUniversity.empty()
        .append(resultAjax.expert_basic.university || '')

      jqThisResearcherCollege.empty()
        .append(resultAjax.expert_basic.college || '')

      // jqThisResearcherSubject.empty()
      //   .append(resultAjax.expert_basic.sub_list || '')

      jqThisResearcherTheme.empty()
        .append(resultAjax.expert_basic.theme_list || '')

      jqCurrentPage.empty()
        .append(this.paperPageCurrent)

      jqTotalPages.empty()
        .append(this.paperPageTotal)

      // 
      if (!jqPaperPreviousPage.hasClass('disabled')) {
        jqPaperPreviousPage.addClass('disabled')
      }

      // ,
      if (this.paperPageTotal === 1 && !jqPaperNextPage.hasClass('disabled')) {
        jqPaperNextPage.addClass('disabled')
      }

      // ,
      let expertImage = resultAjax.expert_basic.img_url
      if (expertImage && expertImage !== '') {
        jqThisResearcherImage.attr({'src': expertImage})
      } else {
        // 
        jqThisResearcherImage.attr({'src': '../../static/asset/image/default_expert_image.png'})
      }

      // 
      let expertResume = resultAjax.expert_basic.resume
      if (expertResume && expertResume !== '') {
        jqThisResearcherResume.empty().append('<p>' + expertResume + '</p>')
      } else {
        jqThisResearcherResume.empty().append('<p>.</p>')
      }

      // 10(10,1)
      let paperItem
      htmlTemplate = ''
      let firstPagePapers = resultAjax.papers.slice(0, 10)
      firstPagePapers.forEach(function (item, index) {
        // JSON
        paperItem = JSON.parse(item)

        // authors
        let paperAuthorsName = paperItem.authors.split(' ').slice(0, 5)

        // id(id,)
        let paperAuthorsId = []
        if (paperItem.author1 && (paperItem.author1 !== '')) {
          paperAuthorsId.push(paperItem.author1)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author2 && (paperItem.author2 !== '')) {
          paperAuthorsId.push(paperItem.author2)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author3 && (paperItem.author3 !== '')) {
          paperAuthorsId.push(paperItem.author3)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author4 && (paperItem.author4 !== '')) {
          paperAuthorsId.push(paperItem.author4)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author5 && (paperItem.author5 !== '')) {
          paperAuthorsId.push(paperItem.author5)
        } else {
          paperAuthorsId.push('')
        }

        // html
        htmlTemplate += '<li><article><header><h3><a>' + paperItem.title + '</a></h3></header>'

        // 
        htmlTemplate += '<p>'
        let i
        for (i = 0; i < paperAuthorsName.length; i++) {
          if (paperAuthorsId[i] === '') {
            // id,,
            htmlTemplate += (paperAuthorsName[i] || paperAuthorsId[i]) + ' , '
          } else {
            htmlTemplate += '<a class="paper-author-item" data-id="' + paperAuthorsId[i] + '">' +
              (paperAuthorsName[i] || paperAuthorsId[i]) + '</a>' + ' , '
          }
        }
        // ' , '
        htmlTemplate = htmlTemplate.substring(0, (htmlTemplate.length - 3))

        // 
        htmlTemplate += ' | ' + (paperItem.type || '') + ' | ' + paperItem.source + '</p>'

        htmlTemplate += '<p><strong>' + '</strong>: ' + paperItem.abstract + '</p>'

        htmlTemplate += '<p><strong>' + '</strong>: ' + paperItem.keyword + '</p>'

        htmlTemplate += '</article></li>'
      })
      jqThisResearcherPapers.empty()
        .append(htmlTemplate)

      // 
      $('#researcher-papers ul.papers a.paper-author-item').click(function () {
        _this.jumpToAnotherResearcher($(this).attr('data-id'))
      })
    },
    // TODO 
    showOpinion (resultAjax) {
      let opinion = resultAjax.opinion_raw
      // console.log('showOpinion ()')
      // console.log(opinion)

      // TODO (social-opinion)
      let jqSocialOpinion = $('div#social-opinion')
      let html = '<h3><strong></strong></h3>'
      if (!(opinion.content === null || opinion.content === '' || opinion.content === false)) {
        // let id = ''
        // let expertId = ''
        // let expertName = ''
        // let expertUniversity = ''
        // let expertCollege = ''
        let contentList = []
        let articleUrlList = []
        let articleTitleList = []
        let articleSourceList = []
        let articleTimeList = []
        let articleBriefList = []
        let articleCount

        // split 
        // if (!(opinion.id === null || opinion.id === '' || opinion.id === false)) {
        //   id = opinion.id
        // }
        // if (!(opinion.expert_id === null || opinion.expert_id === '' || opinion.expert_id === false)) {
        //   expertId = opinion.expert_id
        // }
        // if (!(opinion.expert_name === null || opinion.expert_name === '' || opinion.expert_name === false)) {
        //   expertName = opinion.expert_name
        // }
        // if (!(opinion.expert_university === null || opinion.expert_university === '' ||
        //   opinion.expert_university === false)) {
        //   expertUniversity = opinion.expert_university
        // }
        // if (!(opinion.expert_college === null || opinion.expert_college === '' || opinion.expert_college === false)) {
        //   expertCollege = opinion.expert_college
        // }
        if (opinion.content.indexOf('<edms/>') !== -1) {
          contentList = opinion.content.split('<edms/>')
        }
        if (opinion.article_url.indexOf(',') !== -1) {
          articleUrlList = opinion.article_url.split(',')
        }
        if (opinion.article_title.indexOf('<edms/>') !== -1) {
          articleTitleList = opinion.article_title.split('<edms/>')
        }
        if (opinion.article_source.indexOf('<edms/>') !== -1) {
          articleSourceList = opinion.article_source.split('<edms/>')
        }
        if (opinion.article_time.indexOf('<edms/>') !== -1) {
          articleTimeList = opinion.article_time.split('<edms/>')
        }
        if (opinion.article_brief.indexOf('<edms/>') !== -1) {
          articleBriefList = opinion.article_brief.split('<edms/>')
        }

        articleCount = Math.min(contentList.length, articleUrlList.length, articleTitleList.length,
          articleSourceList.length, articleTimeList.length, articleBriefList.length)
        // console.log(articleCount)

        // HTML Sentences
        if (articleCount > 0) {
          for (let i = 0; i < articleCount; i++) {
            html += '<p>' + '<strong></strong>' + '<a href="' + articleUrlList[i] + '" target="_blank">' +
              articleTitleList[i] + '</a></p>'
            html += '<p>' + '<strong></strong>' + articleTimeList[i] +
              '&nbsp;&nbsp;<strong></strong>' + articleSourceList[i] + '</p>'
            html += '<p>' + '<strong></strong>' + articleBriefList[i] + '</p>'
            html += '<hr />'
          }
        }
      }

      // console.log(html)
      jqSocialOpinion.empty()
        .append(html)

