            } else {
              // 
              console.error(' edge  node')
              return ''
            }
          }
        }
      }

      // 
      myChart.setOption(option)

      // click
      // :(event: Object)
      // contextthis
      myChart.on('click', function (handler) {
        // console.log(handler)

        // // 
