    font-weight: 400;
    letter-spacing: 0.25em;
    text-transform: uppercase;
  }

  #header .main {
    height: inherit;
    line-height: inherit;
    text-align: right;
  }

  #header .main ul {
    height: inherit;
    line-height: inherit;
  }

  #header .main ul li {
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    height: inherit;
    line-height: inherit;
    white-space: nowrap;
  }

  #header .main ul li > * {
    display: block;
    float: left;
  }

  #header .main ul li > a {
    text-decoration: none;
    cursor: pointer;
    border-bottom: 0;
