<script>
import $ from 'jquery'
import breakpoints from '../../static/js/breakpoints.min'
import ExpertProfile from './ExpertProfile'
import HottestExpert from './HottestExpert'
import Menu from './Menu'

export default {
  name: 'Index',
  data () {
    return {
      indexPageData: {}, // 
      expertQueryList: [], // 
      queryCondition: {}, // 
      expertQueryListPageTotal: 0, // -
      expertQueryListPageCurrent: 0, // -
      activeHottestExpert: {}, // 
      showMenu: false // 
    }
  },
  // , 
  watch: {
    'showMenu': 'showMenuChange'
  },
  components: {
    'expert-profile': ExpertProfile,
    'hottest-expert': HottestExpert,
    'menu': Menu
  },
  // 
  beforeCreate () {
    // console.log('Index.vue beforeCreate  ===============>')
    // console.log(this.$el)
  },
  created () {
    // console.log('Index.vue created  ===============>')
    //  store  Action axios 
    // this.$store.dispatch('getIndexPageDataAction')
  },
  beforeMount () {
    // console.log('Index.vue beforeMount  ===============>')
    // console.log(this.$el)
  },
  mounted () {
    // console.log('Index.vue mounted  ===============>')
    // console.log(this.$el)

    // Vue 
    this.indexPageData = {}
    this.expertQueryList = []
    this.queryCondition = {}
    this.expertQueryListPageTotal = 0
    this.expertQueryListPageCurrent = 0
    this.activeHottestExpert = {
      class1: 'A', //  A B C D E
      class2: 'F', //  F G H I J
      class3: 'K', //  K N O P Q
      class4: 'R', //  R S T
      class5: 'U' //  U V X Z
    }
    this.showMenu = false

    // AJAX 
    this.loadHomePage()
    this.init()
  },
  beforeUpdate () {
    // console.log('Index.vue beforeUpdate  ===============>')
    // console.log(this.$el)
  },
  updated () {
    // console.log('Index.vue updated  ===============>')
    // console.log(this.$el)
  },
  beforeDestroy () {
    // console.log('Index.vue beforeDestroy  ===============>')
    // console.log(this.$el)
  },
  destroyed () {
    // console.log('Index.vue destroyed  ===============>')
    // console.log(this.$el)
  },
  methods: {
    //  this.showMenu 
    showMenuChange () {
      // console.log(this.showMenu)
    },
    // 
    toggleShowMenu () {
      // console.log('toggleShowMenu () ', this.showMenu)
      this.showMenu = !this.showMenu

      // let _this = this
      let jqMainModule = $('article.main-module')
      if (this.showMenu) {
        jqMainModule
          .css({'opacity': '0.4'})
        // .click(_this.toggleShowMenu) // 
      } else {
        jqMainModule
          .css({'opacity': '1'})
        // .click(function () { // 
        //   event.stopPropagation()
        // })
      }
    },
    // TODO 
    login () {
      'use strict'

      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()
      let choice = jqLogin.text()

      if (choice === '') {
        if (confirm('')) {
          jqUsername.show()
          jqPassword.show()
          jqLogin.text('')
          // 
          if (jqRegister.hasClass('disabled')) {
            jqRegister.removeClass('disabled')
          }
          // 
          if (this.$store.state.loginFlag) {
            this.$store.dispatch('changeLoginFlag', false)
          }
        }
      } else if (choice === '') {
        if (!username || username === '') {
          console.log('')
          jqUsername.focus()
        } else if (!password || password === '') {
          console.log('')
          jqPassword.focus()
        } else {
          if (username === 'yinyuwei' && password === '12345678') {
            // TODO login 
            jqUsername.hide()
            jqPassword.hide()
            jqLogin.text('')
            // 
            if (!jqRegister.hasClass('disabled')) {
              jqRegister.addClass('disabled')
            }
            // 
            if (!this.$store.state.loginFlag) {
              this.$store.dispatch('changeLoginFlag', true)
            }
            console.log('')
          } else {
            console.log('')
          }
        }
      } else {
        console.error('choice error !!!')
      }
    },
    // TODO 
    register () {
      'use strict'

      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()

      if (!username || username === '') {
        console.log('')
        jqUsername.focus()
      } else if (!password || password === '') {
        console.log('')
        jqPassword.focus()
      } else {
        // 
        let usernamePattern = /^[a-zA-Z_][0-9a-zA-Z_]+$/ // 
        let passwordPattern = /^[\S]+$/ // \S ,  [^\f\n\r\t\v]
        if (username.length < 6) {
          console.log('6')
          jqUsername.focus()
        } else if (username.length > 16) {
          console.log('16')
          jqUsername.focus()
        } else if (password.length < 8) {
          console.log('8')
          jqPassword.focus()
        } else if (password.length > 18) {
          console.log('18')
          jqUsername.focus()
        } else if (password.indexOf(username) !== -1) {
          console.log('')
          jqUsername.focus()
        } else if (!usernamePattern.test(username)) {
          console.log('')
          jqUsername.focus()
        } else if (!passwordPattern.test(password)) {
          console.log('')
          jqPassword.focus()
        } else if (username === 'yinyuwei' && password === '12345678') {
          // TODO register 
          jqUsername.hide()
          jqPassword.hide()
          jqLogin.text('')
          // 
          if (!jqRegister.hasClass('disabled')) {
            jqRegister.addClass('disabled')
          }
          // 
          if (!this.$store.state.loginFlag) {
            this.$store.dispatch('changeLoginFlag', true)
          }
          console.log('')
        } else {
          console.log('')
        }
      }
    },
    // AJAX 
    loadHomePage: function () {
      'use strict'
      let _this = this

      let startDate = new Date()
      let endDate

      let serverUrl = '/'

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        dataType: 'json',
        // async: true, // 
        // timeout: 10000, // 10
        beforeSend: function (xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          console.log('$.ajax beforeSend, state = ' + xhr.status)
          // loading
          $('div.load-container').css({'display': 'block'})
        },
        success: function (result, status, xhr) {
          endDate = new Date()
          console.log(endDate - startDate)

          console.log('$.ajax success, state = ' + xhr.status)
          // loading
          $('div.load-container').css({'display': 'none'})

          if (!result || result === '' || result.length === 0) {
            // TODO
            $.toast({
              text: '',
              showHideTransition: 'plain',
              bgColor: 'red',
              textColor: '#e0e0e0',
              allowToastClose: false,
              hideAfter: 2400,
              stack: 5,
              textAlign: 'center',
              position: 'mid-center'
            })
            console.log('')
            return false
          }

          // 
          _this.indexPageData = {}

          // JSON
          _this.indexPageData.ccn_experts = JSON.parse(result.ccn_experts)
          _this.indexPageData.hottest_five_organizations = []
          _this.indexPageData.hottest_five_experts = []

          let ccnExpertsTemp = _this.indexPageData.ccn_experts
          ccnExpertsTemp.forEach(function (ccnExpertsItem, ccnExpertsIndex) {
            ccnExpertsItem.experts.forEach(function (expertItem, index) {
              _this.indexPageData.ccn_experts[ccnExpertsIndex].experts[index] = JSON.parse(expertItem)
            })
          })

          let hottestFiveOrganizationsTemp = JSON.parse(result.hottest_five_organizations)
          hottestFiveOrganizationsTemp.forEach(function (item, index) {
            _this.indexPageData.hottest_five_organizations.push(JSON.parse(item))
          })

          let hottestFiveExpertsTemp = JSON.parse(result.hottest_five_experts)
          hottestFiveExpertsTemp.forEach(function (item, index) {
            _this.indexPageData.hottest_five_experts.push(JSON.parse(item))
          })

          // console.log(_this.indexPageData.hottest_five_organizations)
          // console.log(_this.indexPageData.hottest_five_experts)

          //  AJAX 
          _this.getHomeAjaxData(_this)

          // 
          result = _this.indexPageData
          _this.renderDetailPage(result)

          endDate = new Date()
          console.log(endDate - startDate)

          return true
        },
        error: function (xhr, status, error) {
          console.log('$.ajax error, state = ' + xhr.status)
          // loading
          $('div.load-container').css({'display': 'none'})
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 
          200: function () {
            console.log('200: ')
          },
          404: function () {
            console.log('404: ')
            alert('404: ')
          },
          500: function () {
            console.log('500: ')
            alert('500: ')
          }
        }
      })
    },
    //  AJAX 
    getHomeAjaxData (_this) {
      // console.log('getHomeAjaxData (_this)')

      this.indexPageData = _this.indexPageData

      console.log(this.indexPageData)
    },
    //  AJAX 
    getListAjaxData (_this) {
      // console.log('getListAjaxData (_this)')

      this.expertQueryList = _this.expertQueryList
      this.expertQueryListPageTotal = _this.expertQueryListPageTotal
      this.expertQueryListPageCurrent = _this.expertQueryListPageCurrent

      console.log(this.expertQueryList)
      console.log(this.expertQueryListPageTotal)
      console.log(this.expertQueryListPageCurrent)
    },
    // 
    renderDetailPage (result) {
      'use strict'
      let _this = this

      let ccnExperts = result.ccn_experts
      let hottestFiveExperts = result.hottest_five_experts
      let hottestFiveOrganizations = result.hottest_five_organizations

      if (!ccnExperts || ccnExperts.length === 0 || ccnExperts === '' ||
        !hottestFiveExperts || hottestFiveExperts.length === 0 || hottestFiveExperts === '' ||
        !hottestFiveOrganizations || hottestFiveOrganizations.length === 0 || hottestFiveOrganizations === '') {
        console.log('function renderDetailPage() error: global variables error.')
        return false
      }

      //  & 
      _this.renderExperts('hottest-five-experts', result.hottest_five_experts)
      _this.renderOrganizations('hottest-five-organizations', result.hottest_five_organizations)

      //  A, F, K, R, U ,  result.ccn_experts  0, 5, 10, 15, 18
      let firstShowFieldList = [0, 5, 10, 15, 18]

      // 
      for (let i = 0; i < firstShowFieldList.length; i++) {
        _this.renderExperts('ccn-hottest-expert-' + (i + 1).toString(),
          ccnExperts[firstShowFieldList[i]].experts)
      }
    },
    //  div 
    renderExperts (divId, expertsList) {
      'use strict'
      let _this = this

      if (expertsList === undefined || expertsList === null || !expertsList || expertsList === '') {
        console.log('function renderExperts() error: global variables expertsList error.')
        return false
      }

      //  div 
      let htmlTemplate = ''
      for (let i = 0; i < expertsList.length; i++) {
        let currentExpert = expertsList[i]

        htmlTemplate += '<dl class="expert-profile">'
        let thisScore = parseFloat(currentExpert.score).toFixed(0)
        // URL
        if (!currentExpert.img_url || currentExpert.img_url === '' || currentExpert.img_url === null) {
          htmlTemplate +=
            '<dt class="dt-img"><img src="../../static/asset/image/default_expert_image.png" ' +
            'data-id="' + currentExpert.id + '" alt="researcher image" />' +
            '</dt>' + '<dd><p>' + currentExpert.name + '</p></dd>' +
            // 
            '<dd><p><strong>' + thisScore + '</strong></p></dd>' +
            '<dd><p><strong></strong>' + currentExpert.university + '</p></dd>' +
            '<dd><p><strong></strong>' + currentExpert.college + '</p></dd>' +
            '<dd><p><strong></strong>'
          if (!currentExpert.theme_list || currentExpert.theme_list === '' ||
            currentExpert.img_url === null) {
            htmlTemplate += '</p></dd>'
          } else {
            // htmlTemplate += currentExpert.theme_list.split("", 1)[0] + '</p>' +
            htmlTemplate += currentExpert.theme_list + '</p></dd>'
          }
        } else {
          htmlTemplate +=
            '<dt class="dt-img">' + '<img src="' + currentExpert.img_url + '" ' +
            'data-id="' + currentExpert.id + '" alt="researcher image" />' +
            '</dt>' + '<dd><p>' + currentExpert.name + '</p></dd>' +
            '<dd><p><strong>' + thisScore + '</strong></p></dd>' +
            '<dd><p><strong></strong>' + currentExpert.university + '</p></dd>' +
            '<dd><p><strong></strong>' + currentExpert.college + '</p></dd>' +
            '<dd><p><strong></strong>'
          if (!currentExpert.theme_list || currentExpert.theme_list === '' ||
            currentExpert.theme_list === null || currentExpert.theme_list === undefined) {
            htmlTemplate += '</p></dd>'
          } else {
            // htmlTemplate += currentExpert.theme_list.split("", 1)[0] + '</p>' +
            htmlTemplate += currentExpert.theme_list + '</p></dd>'
          }
        }
        htmlTemplate += '</dl>'
      }

      //  div 
      $('#' + divId + ' div.expert-profile-list').empty()
      //  div 
        .append(htmlTemplate)

      // 
