    // 
    queryFunction () {
      'use strict'
      let queryType = $('#query-type').val().trim()

      if (queryType === 'normal') {
        let queryInput = $("input[name='query-input']").val().trim()
        let querySelection = $('#query-selection').val().trim()
        console.log('queryFunction() query-input=' + queryInput +
          '&query-type=' + queryType +
          '&query-selection=' + querySelection)
        if (!queryInput || queryInput === '' || !querySelection || querySelection === '') {
          return false
        } else {
          let postData = {}
          postData.query_input = queryInput
          postData.query_type = queryType
          postData.query_selection = querySelection
          postData.researcher_input = ''
          postData.field_input = ''
          postData.research_content_input = ''
          postData.organization_input = ''

          this.queryCondition = postData
          this.queryListAjax(postData, 1)
        }
      } else if (queryType === 'advanced') {
        let researcher = $('#researcher-input').val().trim()
        let field = $('#field-input').val().trim()
        let researchContent = $('#research-content-input').val().trim()
        let organization = $('#organization-input').val().trim()
        console.log(
          'queryFunction() query-type=' + queryType +
          '&researcher-input=' + researcher +
          '&field-input=' + field +
          '&research-content-input=' + researchContent +
          '&organization-input=' + organization)
        if ((!researcher && !field && !researchContent && !organization) ||
          (researcher === '' && field === '' && researchContent === '' && organization === '')) {
          return false
        } else {
          let postData = {}
          postData.query_input = ''
          postData.query_type = queryType
          postData.query_selection = ''
          postData.researcher_input = researcher
          postData.field_input = field
          postData.research_content_input = researchContent
          postData.organization_input = organization

          this.queryCondition = postData
          this.queryListAjax(postData, 1)
        }
      } else {
        return false
      }
    },
