    init () {
      // this
      let _this = this

      // inputenter
      $('#query-input').keydown(function (event) {
        // Enter
        if (event.keyCode === 13 || event.key === 'Enter') {
          _this.queryFunction()
        }
      })

      // if (_this.isChrome() && window.history && window.history.pushState) {
      //   $(window).on('popstate', function () {
