      // ccn-selection-list 
      $('div.ccn-selection-list ul li').click(function () {
        let jqThis = this
        $(jqThis).addClass('active').siblings().removeClass('active')
        //  ccn-hottest-expert-content 
        let ccnExperts = _this.indexPageData.ccn_experts
        let showField = _this.ccnToList($(jqThis).attr('id'))
        let divIndex = _this.listToDiv(showField)

        _this.renderExperts('ccn-hottest-expert-' + divIndex.toString(), ccnExperts[showField].experts)
      }).mouseover(function () {
        $(this).css({'background-color': '#87cefa'})
      }).mouseout(function () {
        $(this).css({'background-color': '#fff'})
      })

      // query-selection-list 
      $('div.query-selection-list ul li').click(function () {
        let jqThis = this
        $(jqThis).addClass('active').siblings().removeClass('active')
        // 
        $('input#query-selection').val($(jqThis).attr('id'))
      }).mouseover(function () {
        $(this).css({'background-color': '#87cefa'})
      }).mouseout(function () {
        $(this).css({'background-color': '#fff'})
      })

      // hover: change background-color
      $('#query-button').mouseover(function () {
        $(this).css('background-color', '#6495ed')
      }).mouseout(function () {
        $(this).css('background-color', '#87cefa')
      })

      $('#query-type-button').mouseover(function () {
        $(this).css({'background-color': '#6495ed'})
      }).mouseout(function () {
        $(this).css({'background-color': '#87cefa'})
      })

      // inputplaceholder
      $('input#query-input').focus(function () {
        $('input#query-input').attr({'placeholder': ''})
      }).blur(function () {
        $('input#query-input').attr({'placeholder': ''})
      })

      let jqueryWindow = $(window)
      let jqueryBody = $('body')
      let jqueryMenu = $('#menu')
      let jquerySidebar = $('#sidebar')
      let jqueryMain = $('#main')

      // Breakpoints.
      breakpoints({
        xlarge: ['1281px', '1680px'],
        large: ['981px', '1280px'],
        medium: ['737px', '980px'],
        small: ['481px', '736px'],
        xsmall: [null, '480px']
      })

      // Play initial animations on page load.
      jqueryWindow.on('load', function () {
        window.setTimeout(function () {
          jqueryBody.removeClass('is-preload')
        }, 100)
      })

      // Menu.
      jqueryBody.append(jqueryMenu)
      // jqueryMenu.panel({
      //   delay: 500,
      //   hideOnClick: true,
      //   hideOnSwipe: true,
      //   resetScroll: true,
      //   resetForms: true,
      //   side: 'right',
      //   target: jqueryBody,
      //   visibleClass: 'is-menu-visible'
      // })
      jqueryMenu.attr('jqueryMenu', 'is-menu-visible')

      // Search (header).
      let jquerySearch = $('#search')
      let jquerySearchInput = jquerySearch.find('input')

      jqueryBody
        .on('click', '[href="#search"]', function (event) {
          event.preventDefault()

          // Not visible?
          if (!jquerySearch.hasClass('visible')) {
            // Reset form.
            jquerySearch[0].reset()

            // Show.
            jquerySearch.addClass('visible')

            // Focus input.
            jquerySearchInput.focus()
          }
        })

      jquerySearchInput
        .on('keydown', function (event) {
          if (event.keyCode === 27) {
            jquerySearchInput.blur()
          }
        })
        .on('blur', function () {
          window.setTimeout(function () {
            jquerySearch.removeClass('visible')
          }, 100)
        })

      // Intro.
      let intro = $('#intro')

      // Move to main on <=large, back to sidebar on >large.
      breakpoints.on('<=large', function () {
        intro.prependTo(jqueryMain)
      })

      breakpoints.on('>large', function () {
        intro.prependTo(jquerySidebar)
      })
    }
  }
}
</script>

